.nav-contain-background {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.nav-logo {
  padding: var(--nav-element-spacing-vertical) var(--nav-element-spacing-horizontal);
}

@media (min-width: 768px) {
  .nav-contain-background {
    background-color: rgba(11, 114, 133, 0.5);
    backdrop-filter: saturate(180%) blur(10px);
    top: 0;
    position: sticky;
    position: -webkit-sticky;
  }
}

[data-theme="dark"] nav a {
  color: var(--colar-gray-4);

  &:hover {
    color: var(--colar-gray-2);
  }
}

nav a {
  color: var(--colar-gray-9);

  &:hover {
    color: var(--colar-gray-11);
  }
}

details[role="list"][open] summary::before {
  height: 100vh;
}

[data-theme="dark"] summary[role="link"].secondary,
a.secondary {
  color: var(--colar-gray-4);
  &:is([aria-current], :hover, :active, :focus) {
    background-color: transparent;
    color: var(--colar-gray-3);
  }
}

summary[role="link"].secondary,
a.secondary {
  color: var(--colar-gray-9);
  &:is([aria-current], :hover, :active, :focus) {
    background-color: transparent;
    color: var(--colar-gray-11);
  }
}

details[role="list"] summary + ul,
li[role="list"] > ul {
  right: unset;
}

details {
  margin-bottom: 0;
}

details[role="list"] {
  border-bottom: none;
  padding: var(--nav-element-spacing-vertical) var(--nav-element-spacing-horizontal);
}

.dropdown-container {
  display: flex;
}

//   // toggle switch

.containToggle {
  display: flex;
  flex-direction: column;
}

.toggleLabel {
  font-size: 0.75em;
  display: block;
  text-align: right;
}

.toggle-switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 25px;
}
.toggle-switch input[type="checkbox"] {
  display: none;
}
.toggle-switch .switch {
  position: absolute;
  cursor: pointer;
  background-color: var(--colar-cyan-8);
  border-radius: 25px;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: background-color 0.2s ease;
}
.toggle-switch .switch::before {
  position: absolute;
  content: "";
  left: 2px;
  top: 2px;
  width: 21px;
  height: 21px;
  background-color: var(--colar-cyan-4);
  border-radius: 50%;
  transition: transform 0.3s ease;
}
.toggle-switch input[type="checkbox"]:checked + .switch::before {
  transform: translateX(25px);
  background-color: var(colar-cyan-7);
}
.toggle-switch input[type="checkbox"]:checked + .switch {
  background-color: var(colar-cyan-2);
}
