.about article {
  margin-bottom: 0.5rem;
}

.origin-bck {
  background-color: #374956;
  background-image: url("../../../assets/idea.jpg");
  background-position: center;
  background-size: cover;
}

.resource-links {
  display: flex;
  gap: 0.5em;
}

@media (min-width: 768px) {
  .about button {
    margin: 0 auto;
  }
}

@media (min-width: 992px) {
  .aboutme-bck {
    margin: 0 auto;
    background-color: #374956;
    background-image: url("../../../assets/me4.jpg");
    background-position: center;
    background-size: cover;
    width: 400px;
    height: 400px;
    border-radius: 50%;
  }
}

.textBox {
  min-width: 100%;
  height: 100%;
}

#resources span {
  display: block;
}
